/* Stili globali, inclusi gli stili per lo sfondo della scena 3D */
body, html {
  margin: 0;
  padding: 0;
  height: 100%;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}



code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}



.App {
  position: relative;
  width: 100vw;
  overflow-x: hidden;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  z-index: 2; /* Più alto dello sfondo per assicurarsi che il contenuto sia sopra */
}

.full-screen-section {
  height: 100vh;
  width: 100vw;
  display: flex;
  justify-content: center;
  align-items: center;
}
/* App.css */
.text-section {
  
  padding: 20px;
  background-color: #f5f5f5; /* Scegli un colore di sfondo se necessario */
  text-align: center; /* Centra il testo se desiderato */
  margin: 20px 0; /* Aggiunge spazio sopra e sotto la sezione di testo */
  z-index: 3;

}

.scene-container {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  z-index: 1; /* Sfondo, quindi uno z-index basso */
}

.contenuto-testuale {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 3;
  color: white;
  text-align: center;
  width: 100%;
  max-width: 600px;
  margin: 0 auto;
}

.carousel-section {
  min-height: 70vh;
  width: 100vw;
  position: relative;
  top: 800px;
  bottom: 200px;
  z-index: 2;
  background-color: transparent;
  margin-top:-30vh;
}


@media (max-width: 768px) {
  /* App.css */
  .carousel-container {
    overflow-x: hidden;
    min-width: 100vw;
    display: flex;
    max-height: 50vh; /* Altezza relativa per adattarsi meglio a schermi di diverse dimensioni */
    align-items: center;
    flex-wrap: nowrap;
    flex-direction: row;
    justify-content: center;
    padding: 0;
    margin: auto;
    -webkit-overflow-scrolling: touch;
    scroll-snap-type: x mandatory;
    scroll-behavior: smooth;
    background-color: transparent;
    z-index: 3;
    margin-top: 0; /* Rimuovi il margine superiore su dispositivi mobili */
  }

  .carosello-immagine-wrapper {
    height: 60%;
    overflow: hidden;
  }

  .carosello-immagine {
    width: 100%;
    height: auto;
    display: block;
    margin: 0 auto;
  }
}

/* Media query per adattare le dimensioni delle immagini su schermi più grandi */
@media (min-width: 768px) {
  .carosello-immagine {
    width: 250px;
    height: auto;
  }
}

@media (min-width: 1024px) {
  .carosello-immagine {
    width: 300px;
  }
}

/* Opzionale: Media query per schermi più piccoli */
@media (max-width: 480px) {
  .carosello-immagine {
    width: 150px; /* Riduci ulteriormente la larghezza delle immagini su schermi molto piccoli */
  }
}




.qr-code-container {
  position: flex;
  bottom: 10%;
  left: 0%;
  margin-bottom: 20px;
  margin-top: 500px; /* Aggiunge spazio sotto al carosello */
  display: flex;
  justify-content: center;
  align-items: center;
  position: relative;
  z-index: 1;
  background-color: transparent;
}


@keyframes fadeIn {
  from { opacity: 0; }
  to { opacity: 100; }
}

.qr-code-animate {
  animation: fadeIn 2s ease-out forwards;
}

.qr-code-image {
  max-width: 50%;
  height: auto;
}


.box-semi-trasparente {
  font-weight: bold;
  color: rgb(255, 255, 255);
  text-shadow: -1px -1px 0 #000, 1px -1px 0 #000, -1px 1px 0 #000, 0.5px 0.5px 0 #000;
  background-color: rgba(128, 0, 128, 0.5);
  padding: 20px;
  border-radius: 10px;
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  z-index: 2;
  width: 80%;
  max-height: 90vh;
  overflow-y: auto;
  box-shadow: 0 4px 8px rgba(19, 255, 11, 0.279);
}

.affermazione-contestazione {
  display: flex;
  justify-content: space-between;
  margin-bottom: 20px;
}

.affermazione, .contestazione {
  flex: 1;
}

.affermazione {
  margin-right: 10px;
}

.contestazione {
  margin-left: 10px;
}

.contestazione img {
  max-width: 100%;
  height: auto;
  margin-top: 10px;
}

/* Nuove regole per il layout a due colonne */
.capitolo-container {
  display: flex;
  gap: 20px;
}

.colonna {
  flex: 1;
}

.colonna-testo, .colonna-immagini {
  display: flex;
  flex-direction: column;
  gap: 10px;
}

/* Regole per ridimensionare tutte le immagini e i video a una stessa dimensione */
.colonna-immagini img, .colonna-immagini video {
  object-fit: contain;
  border-radius: 10px;
  width: 100%;
  height: auto;
  aspect-ratio: 4 / 3; /* Esempio per un rapporto d'aspetto diverso */
}
