/* Cap5.css */

.titolo-capitolo {
    text-align: center;
    margin-bottom: 20px;
  }
  
  .box-semi-trasparente {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .sezione {
    display: grid;
    grid-template-columns: 1fr 1fr; /* Due colonne di uguale dimensione */
    gap: 20px;
    align-items: center; /* Allinea verticalmente il contenuto delle celle */
  }
  
  .testo-media {
    grid-column: 1; /* Posiziona il testo nella prima colonna */
    display: flex;
    flex-direction: column;
    justify-content: center; /* Centra verticalmente il testo */
  }
  
  .media {
    grid-column: 2; /* Posiziona i media nella seconda colonna */
    width: 100%;
    border-radius: 10px; /* Angoli arrotondati per i media */
    object-fit: cover; /* Assicura che il contenuto copra l'intera area senza distorcere */
  }
  
  .testo-media p {
    margin: 0;
    padding: 5px;
  }
  