/* Cap4.css */
.box-semi-trasparente {
    display: grid;
    grid-template-columns: 1fr;
    gap: 20px;
  }
  
  .sezione-video, .sezione-immagine {
    display: grid;
    grid-template-columns: 1fr 1fr;
    gap: 20px;
    align-items: center;
  }
  
  .media, .testo-media {
    width: 100%;
    border-radius: 10px;
  }
  
  .testo-media {
    grid-column: 1; /* Posiziona il testo nella prima colonna */
  }
  
  .media {
    grid-column: 2; /* Posiziona i media nella seconda colonna */
  }
  
  .testo-media p {
    margin: 0;
    padding: 5px;
  }
  